.site-post{
    display: block;
    background: var(--astr-background-color);
    article{
        padding: 2rem;
        max-width: 70vw;
        .post-intro{
            color: grey;
            text-align: center;
        }
        h2{
            text-decoration: none;
            font-size: 32;
            font-weight: 600;
            margin: 2rem;
            border-bottom: solid;
            border-right: solid;
            padding: 1.5rem;
        }
        .post-header{
            text-align: center;
            text-transform: uppercase;
            text-shadow: 0 0 15px #9393f5;
        }
        .exerpt{
            margin: 1rem;
            color: lightgray;
        }
        img{
            margin: 0 25%;
        }
    }
}

@media screen and (max-width: 1000px){
    .site-post{
        article{
            img{
                margin: 0;
                max-width: 20rem;
            }
        }
    }
}